.projectsBlock {
    min-height: 100vh;
    background-color: #293f50;
}

.projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    border: 1px solid white;
    height: 100%;
    width: 80%;
    margin: 0 auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.title {
    background-color: white;
}

.projects {
    border: 1px solid ;
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}